.thecube{
  position:relative;
  transform:rotateZ(45deg)
}

.thecube .cube{
  position:relative;
  transform:rotateZ(45deg)
}

.thecube .rotateLoading{
  transform:rotateZ(360deg);
  animation:spin 2.4s infinite linear both
}

.thecube .cube{
  float:left;
  width:50%;
  height:50%;
  position:relative;
  transform:scale(1.1)
}

.thecube .cube:before{
  content:'';
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background-color:#fff;
  animation:foldthecube 2.4s infinite linear both;
  transform-origin:100% 100%
}

.thecube .c2{
  transform:scale(1.1) rotateZ(90deg)
}

.thecube .c3{
  transform:scale(1.1) rotateZ(180deg)
}

.thecube .c4{
  transform:scale(1.1) rotateZ(270deg)
}

.thecube .c2:before{
  animation-delay:0.3s
}

.thecube .c3:before{
  animation-delay:0.6s
}

.thecube .c4:before{
  animation-delay:0.9s
}

@keyframes foldthecube{
  0%,10%{
    transform:perspective(140px) rotateX(-180deg);
    opacity:0
  }

  25%,75%{
    transform:perspective(140px) rotateX(0deg);
    opacity:1
  }

  90%,100%{
    transform:perspective(140px) rotateY(180deg);
    opacity:0
  }
}

@keyframes spin{
  from{
    transform:rotate(0deg)
  }

  to{
    transform:rotate(360deg)
  }
}

.select{
  cursor:pointer;
  display:inline-block;
  position:relative;
  font:normal 15px/28px Arial, Sans-Serif;
  color:transparent;
  background-image:url(/static/media/account-edit.84b4538e.svg);
  background-size:cover;
  border:1px solid #ccc
}

.scan{
  animation:moveDown 1.5s ease-in-out alternate-reverse infinite
}

.oscillateVertically{
  animation:moveDownVeryLittle 3s ease-in-out alternate-reverse infinite
}

.rotateIndefinitely{
  transform:rotateZ(360deg);
  animation:spin 2.4s infinite ease-in-out both
}

@keyframes moveDown{
  from{
    transform:translateY(-2rem)
  }

  to{
    transform:translateY(6rem)
  }
}

@keyframes moveDownVeryLittle{
  from{
    transform:translateY(-0.08rem)
  }

  to{
    transform:translateY(0.08rem)
  }
}

